"use client"

import { useEffect, useState } from "react"
import { cn } from "@/lib/utils"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Badge } from "@/components/ui/badge"
import { Plus, Trash2, ShieldCheck, MoreHorizontal, Loader2, UserPlus, Search, Shield, UserCog, GraduationCap, Lock, Mail, Users } from "lucide-react"
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { toast } from "sonner"
import Link from "next/link"

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || "https://localhost:7099/api"

export default function UserManagementPage() {
    const [users, setUsers] = useState<any[]>([])
    const [isLoading, setIsLoading] = useState(true)
    const [isDialogOpen, setIsDialogOpen] = useState(false)
    const [isSubmitting, setIsSubmitting] = useState(false)
    const [searchQuery, setSearchQuery] = useState("")

    // New User Form State
    const [newUser, setNewUser] = useState({
        full_Name: "",
        email: "",
        username: "",
        password: "",
        role: "Staff"
    })

    useEffect(() => {
        fetchUsers()
    }, [])

    const fetchUsers = async () => {
        setIsLoading(true)
        try {
            const token = localStorage.getItem("token")
            const res = await fetch(`${API_BASE_URL}/admin/users`, {
                headers: { "Authorization": `Bearer ${token}` }
            })
            if (res.ok) {
                const data = await res.json()
                setUsers(data)
            }
        } catch (err) {
            toast.error("Failed to fetch users")
        } finally {
            setIsLoading(false)
        }
    }

    const handleCreateUser = async (e: React.FormEvent) => {
        e.preventDefault()
        setIsSubmitting(true)
        try {
            const token = localStorage.getItem("token")
            const res = await fetch(`${API_BASE_URL}/admin/users`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${token}`
                },
                body: JSON.stringify(newUser)
            })

            if (res.ok) {
                toast.success("User created successfully")
                setIsDialogOpen(false)
                fetchUsers()
                setNewUser({ full_Name: "", email: "", username: "", password: "", role: "Staff" })
            } else {
                const error = await res.json()
                toast.error(error.message || "Failed to create user")
            }
        } catch (err) {
            toast.error("Connection error")
        } finally {
            setIsSubmitting(false)
        }
    }

    const handleDelete = async (id: number) => {
        if (!confirm("Are you sure you want to delete this user?")) return

        try {
            const token = localStorage.getItem("token")
            const res = await fetch(`${API_BASE_URL}/admin/users/${id}`, {
                method: "DELETE",
                headers: { "Authorization": `Bearer ${token}` }
            })

            if (res.ok) {
                toast.success("User deleted")
                fetchUsers()
            } else {
                toast.error("Failed to delete user")
            }
        } catch (err) {
            toast.error("Connection error")
        }
    }

    const handlePromote = async (id: number) => {
        try {
            const token = localStorage.getItem("token")
            const res = await fetch(`${API_BASE_URL}/admin/users/promote/${id}`, {
                method: "POST",
                headers: { "Authorization": `Bearer ${token}` }
            })

            if (res.ok) {
                toast.success("User promoted to Admin")
                fetchUsers()
            } else {
                toast.error("Failed to promote user")
            }
        } catch (err) {
            toast.error("Connection error")
        }
    }

    const filteredUsers = users.filter(u =>
        (u.full_Name || "").toLowerCase().includes(searchQuery.toLowerCase()) ||
        (u.username || "").toLowerCase().includes(searchQuery.toLowerCase()) ||
        (u.email || "").toLowerCase().includes(searchQuery.toLowerCase())
    )

    const getRoleIcon = (role: string) => {
        switch (role) {
            case 'Admin': return <Shield className="h-4 w-4" />
            case 'Staff': return <UserCog className="h-4 w-4" />
            case 'Student': return <GraduationCap className="h-4 w-4" />
            case 'Security': return <ShieldCheck className="h-4 w-4" />
            default: return <Lock className="h-4 w-4" />
        }
    }

    return (
        <div className="min-h-screen bg-background p-8">
            <div className="mx-auto max-w-7xl space-y-8">
                <div className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
                    <div>
                        <div className="flex items-center gap-2 mb-1">
                            <Link href="/admin" className="text-sm font-medium text-muted-foreground hover:text-primary transition-colors">Admin</Link>
                            <span className="text-muted-foreground text-sm">/</span>
                            <h1 className="text-2xl font-bold tracking-tight text-foreground">User Management</h1>
                        </div>
                        <p className="text-muted-foreground">Manage accounts for staff, security, and students.</p>
                    </div>

                    <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
                        <DialogTrigger asChild>
                            <Button className="h-10 bg-primary text-primary-foreground shadow-lg hover:shadow-primary/20 transition-all font-bold">
                                <Plus className="mr-2 h-4 w-4" /> Add New User
                            </Button>
                        </DialogTrigger>
                        <DialogContent className="sm:max-w-[425px]">
                            <form onSubmit={handleCreateUser}>
                                <DialogHeader>
                                    <DialogTitle>Add New User</DialogTitle>
                                    <DialogDescription>
                                        Create a new staff or security account here.
                                    </DialogDescription>
                                </DialogHeader>
                                <div className="grid gap-4 py-4">
                                    <div className="grid gap-2">
                                        <Label htmlFor="name">Full Name</Label>
                                        <Input
                                            id="name"
                                            placeholder="John Doe"
                                            value={newUser.full_Name}
                                            onChange={(e) => setNewUser({ ...newUser, full_Name: e.target.value })}
                                            required
                                        />
                                    </div>
                                    <div className="grid gap-2">
                                        <Label htmlFor="email">Email Address</Label>
                                        <Input
                                            id="email"
                                            type="email"
                                            placeholder="john@example.com"
                                            value={newUser.email}
                                            onChange={(e) => setNewUser({ ...newUser, email: e.target.value })}
                                            required
                                        />
                                    </div>
                                    <div className="grid gap-2">
                                        <Label htmlFor="username">Username</Label>
                                        <Input
                                            id="username"
                                            placeholder="johndoe"
                                            value={newUser.username}
                                            onChange={(e) => setNewUser({ ...newUser, username: e.target.value })}
                                            required
                                        />
                                    </div>
                                    <div className="grid gap-2">
                                        <Label htmlFor="password">Password</Label>
                                        <Input
                                            id="password"
                                            type="password"
                                            value={newUser.password}
                                            onChange={(e) => setNewUser({ ...newUser, password: e.target.value })}
                                            required
                                        />
                                    </div>
                                    <div className="grid gap-2">
                                        <Label htmlFor="role">Account Role</Label>
                                        <Select
                                            value={newUser.role}
                                            onValueChange={(v) => setNewUser({ ...newUser, role: v })}
                                        >
                                            <SelectTrigger className="h-11">
                                                <SelectValue placeholder="Select role" />
                                            </SelectTrigger>
                                            <SelectContent>
                                                <SelectItem value="Staff">
                                                    <div className="flex items-center gap-2">
                                                        <UserCog className="h-4 w-4 text-blue-500" />
                                                        <span>Staff / Reception</span>
                                                    </div>
                                                </SelectItem>
                                                <SelectItem value="Security">
                                                    <div className="flex items-center gap-2">
                                                        <Shield className="h-4 w-4 text-emerald-500" />
                                                        <span>Security Guard</span>
                                                    </div>
                                                </SelectItem>
                                                <SelectItem value="Admin">
                                                    <div className="flex items-center gap-2">
                                                        <Lock className="h-4 w-4 text-purple-500" />
                                                        <span>Administrator</span>
                                                    </div>
                                                </SelectItem>
                                                <SelectItem value="Student">
                                                    <div className="flex items-center gap-2">
                                                        <GraduationCap className="h-4 w-4 text-orange-500" />
                                                        <span>Student</span>
                                                    </div>
                                                </SelectItem>
                                            </SelectContent>
                                        </Select>
                                    </div>
                                </div>
                                <DialogFooter>
                                    <Button type="submit" disabled={isSubmitting} className="w-full">
                                        {isSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <UserPlus className="mr-2 h-4 w-4" />}
                                        Create Account
                                    </Button>
                                </DialogFooter>
                            </form>
                        </DialogContent>
                    </Dialog>
                </div>

                <Card className="border-border bg-card shadow-sm border-2">
                    <CardHeader className="pb-4">
                        <div className="flex items-center justify-between">
                            <CardTitle className="text-lg font-semibold flex items-center gap-2">
                                <Users className="h-5 w-5 text-primary" />
                                User Accounts
                            </CardTitle>
                            <div className="relative w-64">
                                <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
                                <Input
                                    placeholder="Search accounts..."
                                    className="pl-10 h-10 bg-background border-border"
                                    value={searchQuery}
                                    onChange={(e) => setSearchQuery(e.target.value)}
                                />
                            </div>
                        </div>
                    </CardHeader>
                    <CardContent>
                        <div className="rounded-lg border border-border overflow-hidden">
                            <Table>
                                <TableHeader className="bg-muted/50">
                                    <TableRow>
                                        <TableHead className="font-semibold">User</TableHead>
                                        <TableHead className="font-semibold">Account Info</TableHead>
                                        <TableHead className="font-semibold">Role</TableHead>
                                        <TableHead className="font-semibold text-right">Actions</TableHead>
                                    </TableRow>
                                </TableHeader>
                                <TableBody>
                                    {isLoading ? (
                                        <TableRow>
                                            <TableCell colSpan={4} className="h-32 text-center">
                                                <div className="flex flex-col items-center gap-2">
                                                    <Loader2 className="h-8 w-8 animate-spin text-primary" />
                                                    <p className="text-sm text-muted-foreground font-medium animate-pulse">Loading accounts...</p>
                                                </div>
                                            </TableCell>
                                        </TableRow>
                                    ) : filteredUsers.length === 0 ? (
                                        <TableRow>
                                            <TableCell colSpan={4} className="h-32 text-center text-muted-foreground font-medium">
                                                <Search className="h-8 w-8 mx-auto mb-2 opacity-20" />
                                                No users found matching your search.
                                            </TableCell>
                                        </TableRow>
                                    ) : filteredUsers.map((u) => (
                                        <TableRow key={u.id} className="group hover:bg-muted/50 transition-all border-border">
                                            <TableCell>
                                                <div className="flex items-center gap-3">
                                                    <div className={cn(
                                                        "h-10 w-10 rounded-full flex items-center justify-center border-2 shadow-sm transition-transform group-hover:scale-105",
                                                        u.role === 'Admin' ? 'bg-purple-500/10 border-purple-500/20 text-purple-500' :
                                                            u.role === 'Staff' ? 'bg-blue-500/10 border-blue-500/20 text-blue-500' :
                                                                u.role === 'Security' ? 'bg-emerald-500/10 border-emerald-500/20 text-emerald-500' :
                                                                    'bg-orange-500/10 border-orange-500/20 text-orange-500'
                                                    )}>
                                                        {getRoleIcon(u.role)}
                                                    </div>
                                                    <div className="flex flex-col">
                                                        <span className="font-semibold text-foreground">{u.full_Name}</span>
                                                        <span className="text-xs text-muted-foreground flex items-center gap-1">
                                                            <Lock className="h-3 w-3" /> @{u.username}
                                                        </span>
                                                    </div>
                                                </div>
                                            </TableCell>
                                            <TableCell>
                                                <div className="flex items-center gap-2 text-sm text-muted-foreground">
                                                    <Mail className="h-3.5 w-3.5" />
                                                    {u.email}
                                                </div>
                                            </TableCell>
                                            <TableCell>
                                                <Badge
                                                    className={cn(
                                                        "gap-1.5 px-3 py-1 font-medium transition-all group-hover:shadow-sm",
                                                        u.role === 'Admin' ? 'bg-purple-500/10 text-purple-600 border-purple-500/20 hover:bg-purple-500/20' :
                                                            u.role === 'Staff' ? 'bg-blue-500/10 text-blue-600 border-blue-500/20 hover:bg-blue-500/20' :
                                                                u.role === 'Security' ? 'bg-emerald-500/10 text-emerald-600 border-emerald-500/20 hover:bg-emerald-500/20' :
                                                                    'bg-orange-500/10 text-orange-600 border-orange-500/20 hover:bg-orange-500/20'
                                                    )}
                                                    variant="outline"
                                                >
                                                    <span className={cn(
                                                        "h-1.5 w-1.5 rounded-full",
                                                        u.role === 'Admin' ? 'bg-purple-500' :
                                                            u.role === 'Staff' ? 'bg-blue-500' :
                                                                u.role === 'Security' ? 'bg-emerald-500' :
                                                                    'bg-orange-500'
                                                    )} />
                                                    {u.role}
                                                </Badge>
                                            </TableCell>
                                            <TableCell className="text-right">
                                                <div className="flex justify-end gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                                                    {u.role !== 'Admin' && (
                                                        <Button
                                                            variant="ghost"
                                                            size="icon"
                                                            className="h-9 w-9 text-primary hover:bg-primary/10 rounded-full shadow-sm"
                                                            title="Promote to Admin"
                                                            onClick={() => handlePromote(u.id)}
                                                        >
                                                            <ShieldCheck className="h-4.5 w-4.5" />
                                                        </Button>
                                                    )}
                                                    <Button
                                                        variant="ghost"
                                                        size="icon"
                                                        className="h-9 w-9 text-destructive hover:bg-destructive/10 rounded-full shadow-sm"
                                                        onClick={() => handleDelete(u.id)}
                                                        title="Delete User"
                                                    >
                                                        <Trash2 className="h-4.5 w-4.5" />
                                                    </Button>
                                                </div>
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </div>
                    </CardContent>
                </Card>
            </div>
        </div>
    )
}
